import { Component, ViewContainerRef, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { routerTransition } from '../router.animations';
import { log } from 'util';
import { HttpClientModule } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { ToasterService} from 'angular5-toaster';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    animations: [routerTransition()]
})
export class LoginComponent implements OnInit {
email: string;
password: string;

response: any;
private toasterService: ToasterService;

    constructor(private http: HttpClient,
        toasterService: ToasterService,
                public router: Router) {
                    this.toasterService = toasterService;
                }

    ngOnInit() {}


    onAuthenticateSubmit() {
        const societyadmin = {
          email: this.email,
          password: this.password
        };
        console.log(societyadmin);
        localStorage.setItem('isLoggedin', 'true');

          // // Authenticate societyadmin
          this.http.post('api/authenticatesocietyadmin', societyadmin).subscribe(data => {

      
        this.response = data;

            if(this.response.success == true){
                this.toasterService.pop('success', this.response.message);       
                localStorage.setItem('isLoggedin', 'true');
                localStorage.setItem('societyadmin', JSON.stringify(this.response.societyadmin));
                if(this.response.manager){
                  localStorage.setItem('manager', JSON.stringify(this.response.manager));
                }
                if(this.response.chairman){
                  localStorage.setItem('chairman', JSON.stringify(this.response.chairman));
                }
                setTimeout(() => 
                {
                  this.router.navigate(['/dashboard']);
                },
                  3000);

          }
          else{
            this.toasterService.pop('error', this.response.message);       
            localStorage.setItem('isLoggedin', '');

          }
            }
          );

      }
}



